<template>
    <app>
        <topBar>
            <topBarButton action="open:mainDrawer">
                menu
            </topBarButton>
            <topBarTitle>
                Magic Manager
            </topBarTitle>
        </topBar>
        <navDrawer name="mainDrawer">
            <navDrawerUser :icon="userIcon" :username="username" :secondary="secondary" />
            <navDrawerButton icon="view-dashboard" navTo="/dashboard">Dashboard</navDrawerButton>
            <navDrawerButton icon="card-search" navTo="/searchCards">Search cards</navDrawerButton>
            <navDrawerButton icon="cards-variant">Sets</navDrawerButton>
            <navDrawerButton icon="cards" navTo="/collection">Your collection</navDrawerButton>
            <navDrawerButton icon="test-tube">Laboratory</navDrawerButton>
            <divider />
            <navDrawerButton icon="login-variant" v-if="!loggedIn" @click="loginToMtgManager()">Login</navDrawerButton>
            <navDrawerButton icon="logout-variant" v-else @click="logout()">Logout</navDrawerButton>
        </navDrawer>
        <mainView>
            <router-view />
        </mainView>
    </app>
</template>

<script>
import vueChannel from "vue-channel";
import { db } from "./js/global.js";
import { auth, firestore } from "./js/firebase.js";
import { app, topBar, topBarButton, topBarTitle, navDrawer, navDrawerUser, navDrawerButton, divider, mainView } from "./components.js";

export default {
    components: {
        app,
        topBar,
        topBarButton,
        topBarTitle,
        navDrawer,
        navDrawerUser,
        navDrawerButton,
        divider,
        mainView
    },
    data() {
        return {
            userIcon: "https://img.scryfall.com/cards/art_crop/front/2/6/266df8c3-5872-4d83-90bc-8f6f854ac838.jpg?1562138723",
            username: "Not logged in",
            secondary: "You can login in below",
            loggedIn: false
        }
    },
    methods: {
        loginToMtgManager() {
            var googleProvider = new auth.GoogleAuthProvider();

            auth().signInWithPopup(googleProvider);
        },
        logout() {
            auth().signOut().then(() => {
                vueChannel("user")
                    .set({});
            });
        }
    },
    async created() {
        vueChannel("user")
            .receive(user => {
                if (user.uid !== undefined) {
                    this.userIcon = user.photoURL;
                    this.username = user.displayName;
                    this.secondary = "You are logged in";
                    this.loggedIn = true;
                } else {
                    this.userIcon = "https://img.scryfall.com/cards/art_crop/front/2/6/266df8c3-5872-4d83-90bc-8f6f854ac838.jpg?1562138723";
                    this.username = "Not logged in";
                    this.secondary = "You can login in below";
                    this.loggedIn = false;
                }
            });

        auth().onAuthStateChanged(user => {
            if (user) {
                vueChannel("user")
                    .set(user);

                db.user = user;

                firestore
                    .collection("users")
                    .doc(user.uid)
                    .collection("data")
                    .doc("cardCollection")
                    .get()
                    .then(doc => {
                        if (doc && doc.exists) {
                            var onlineCollection = doc.data().collection;
                            var localCollection = db.collection;

                            if (localCollection) {
                                db.collection = {
                                    ...localCollection,
                                    ...onlineCollection
                                }
                            } else {
                                db.collection = onlineCollection
                            }
                        }
                    });
            }
        });
    }
}
</script>

<style lang="scss">

@import "./mdl-vue/config.scss";

.scryfallIcon {
    height: 20px;
    width: 20px;

    transform: translateY(4px);

    border-radius: 50%;
    box-shadow: $elevation1dp;
}

button {
    margin: 8px;

    background-color: transparent;
    border: 2px solid $secondaryColor;
    border-radius: 5px;
    outline: 0;
    font-family: "Literata";
    font-weight: 400;
    font-weight: 16px;
    padding: 4px 8px;
    cursor: pointer;

    &:hover {
        color: #ffffff;
        background-color: $secondaryColor;
    }
}

</style>