import vueChannel from "vue-channel";

export default function(action) {
    console.log(action);
    if (action !== undefined) {
        if (action.includes("route:")) {
            console.log(vueChannel(action).state);
        } else if (action.includes("snackbar:")) {
            // snackbar:main:5000
            // snackbar:main

            var parts = action.split(":");

            var duration = 3000;
            if (parts.length === 3) {
                duration = parts[2];
                parts.pop();
            }

            console.log(parts);

            vueChannel(parts.join(":"))
                .set({
                    duration,
                    triggered: Date.now()
                });
        } else {
            vueChannel(action)
                .set({
                    trigger: Math.random()
                });
        }
    }
}