<template lang="html">
	<transition name="bar">
		<div class="snackbar" v-if="internalShow">
            <p><slot></slot></p>
		</div>
	</transition>
</template>

<script>
import vueChannel from "vue-channel";

export default {
	props: ["show", "name"],
	data() {
		return {
			internalShow: false
		}
	},
	watch: {
		show() {
			this.internalShow = this.show === true || this.show === "true";
		}
	},
	created() {
        vueChannel("snackbar:" + this.name)
            .receive(state => {
                var now = Date.now();

                var distance = state.triggered - now;

                if (distance < 100) {
                    this.internalShow = true;

                    setTimeout(() => {
                        this.internalShow = false;
                    }, state.duration);
                }
            });
	}
}
</script>

<style lang="scss" scoped>

@import "./config.scss";

.snackbar {
    height: 48px;
    max-width: 344px;

    padding: 0px 16px;
    position: fixed;
    z-index: 6;
    bottom: 24px;
    left: 24px;

    background-color: $snackbarBackgroundColor;
    border-radius: $snackbarBorderRadius;
    box-shadow: $snackbarElevation;

    p {
        margin: 0;
        padding: 0;
        position: relative;
        top: 50%;
        transform: translateY(-50%);

        font-family: $snackbarFontFamily;
        font-weight: $snackbarFontWeight;
        color: #ffffffde;
    }
}

.bar-enter-active {
	transition: 150ms cubic-bezier(0.0, 0.0, 0.2, 1) all;
}

.bar-leave-active {
	transition: 100ms cubic-bezier(0.0, 0.0, 0.2, 1) all;
}

.bar-enter {
	opacity: 0;
	transform: scale(0.9);
}

.bar-enter-to {
	opacity: 1;
	transform: scale(1);
}

.bar-leave {
    opacity: 1;
}

.bar-leave-to {
    opacity: 0;
}

@media only screen and (max-width: 600px) {
	.snackbar {
        bottom: 8px;
        left: 8px;
        width: calc(100% - 48px);
        max-width: 10000px;
    }
}

</style>
